@import "../../global.scss";

.intro{
    background-color: white;
    display: flex;


    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    .left{
        flex: 0.5;
        overflow: hidden;
        
        .imgContainter{
            position: relative;
            width: 900px;
            height: 900px;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile {
                align-items: flex-start;
                bottom: 10%;
                .logo-container{
                    left: 20%;
                }
            }
        }
    }
    .right{
        flex: 0.5;
        position: relative; 
        overflow: hidden;
        .wrapper{
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                align-items: center;
                padding-left: 0;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                }

            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;

                @include mobile{
                    font-size: 20px;
                }

                span{
                    font-size: 30px;
                    color: crimson;
                }
                
                .ityped-cursor {
                    animation: blink 1s infinite;
                }
            
                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }      
        }
        a {
            position: absolute;
            bottom: 10px;
            left: 40%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                    
                }
            }
        }
    }
}